
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XButton,
Confirm
},
data() {
return {
warning: 10,
info: {},
infolist: [],
title: {
name: "queryHelp",
flag: false,
backUrl: ""
},
infoType: 0,
pushFlag: false,
drawerVisibility: false,
showModeValue: "push",
showPlacement: "right",
showPlacementValue: "right",
temptTopScroll: 0,
swcount: 0,
confirmtitle: '',
delexId: '',
showconfirm: false,
callphonenum: "",
showinfo: true,
requestUrl: "",
isAndroid: ""
};
},
props: ["infoObj"],
created() {
console.log("ceshi==>" + this.$i18n.locale)
var infoid = this.infoObj;
var _this = this;
this.requestUrl = _this.$myConfig.ip + ":" + _this.$myConfig.port;
var i18nStr = window.localStorage.getItem('i18nStr');
if(i18nStr == undefined) {
console.log("i18nStr is null ")
this.$i18n.locale = "zh-CN"; 
} else {
console.log("i18nStr is not null ")
this.$i18n.locale = i18nStr; 
}

this.isAndroid = this.$isAndroid();
if(this.isAndroid) {

window.android.enableJsBackPressed();
} else {

console.log("is ios pageinit")
window.location.href = "passparam:yes"

}
},
mounted: function() {
this.initSpan();
window.goBack = this.goBack; 
window.setI18nValue = this.setI18nValue; 
},
methods: {
setI18nValue(i18nStr) {
this.$i18n.locale = i18nStr;
},
goAnchor(selector) {
this.drawerVisibility = !this.drawerVisibility;
var anchor = this.$el.querySelector(selector)




document.body.scrollTop = anchor.offsetTop;
document.getElementsByClassName("contents")[0].scrollTop = anchor.offsetTop;
console.log("赋值完成")


},
jumpWeb() {
let webUrl = "http://www.spu.gov.mo";
console.log("param==> open web==>" + webUrl)
window.android.openZoomActivity(this.$t("jingchaWeb"), webUrl)
},
initSpan() {
let _this = this;
$(".li_right,.li_right_m").find("span").click(function() {

let phoneNum = $(this).text();
_this.callphone(phoneNum)
})
},
callphone(phone) {
this.callphonenum = phone;
var _this = this;
_this.$confirm(_this.$t('callphone') + "：" + _this.callphonenum, '', {
okLabel: _this.$t('callout'),
cancelLabel: _this.$t('cancel'),
}).then(({
result
}) => {
if(result) {
console.log("param==sure>")







let anchor = document.createElement("a");
anchor.setAttribute("href","tel:"+_this.callphonenum);
anchor.click();
} else {
console.log('取消')
}
});

},
cancelHide() {

this.showMenu = false;
},
onConfirm() {
var _this = this;
console.log("确认" + _this.callphonenum)
_this.showconfirm = false;
setTimeout(() => {
if(_this.isAndroid) {
window.android.callPhone(_this.callphonenum)
} else {
window.location.href = "callNumer:" + _this.callphonenum;
}
}, 150)



},
onShow() {
console.log("显示")
},
onHide() {
console.log("隐藏")
},
requestData() {
var _this = this;
_this.$vux.loading.show({
text: 'Loading'
}) //打開正在加載
var qurl = _this.requestUrl + "enquiriesrescuecalls/enquiriesrescuecalls"
var lang = "zh";
if(_this.$i18n.locale == 'pt-PT') {
lang = "pt";
} else if(_this.$i18n.locale == 'en-US') {
lang = "en"
}
var param = {
"key": _this.$myConfig.key,
"language": lang
}
_this.$commonAjax("GET", qurl, param, function(data) {
console.log("come kp")
_this.$vux.loading.hide(); 
console.log(data);
_this.infolist = JSON.parse(data.typeList);
_this.infolist = _this.infolist.typeList;
console.log(_this.infolist)

}, function() {
_this.showBottomToast("请求数据失败！")
var appData = require("../../assets/json/planinfo.json");
console.log(appData)
_this.infolist = appData.typeList;
_this.$vux.loading.hide(); 
}, true)
},
openside() {
console.log("赋值")
let this_obj = document.getElementsByClassName("contents")[0];
console.log(this_obj)
console.log(this.drawerVisibility);
if(!this.drawerVisibility) {
if(this.infolist.length > 0) {
this.temptTopScroll = this_obj.scrollTop;
this.drawerVisibility = !this.drawerVisibility
}


/*setTimeout(()=>{
},500)*/


} else {


this.drawerVisibility = !this.drawerVisibility
}

},
goBack() {
console.log(this.title);
var backUrl = this.title.backUrl;
console.log("title==>" + backUrl)
if(backUrl != undefined && backUrl != null && backUrl != 'null' && backUrl != '') {
this.$router.push(backUrl);
} else {
this.$router.go(-1); 
}

},
moveEv(e) {
this.swcount++;
if(this.swcount > 5) {
console.log("进入侧滑")
let pageX = e.changedTouches[0].pageX;
/*if($(".vux-drawer-active").length==0){
if(pageX<this.pageXI){
console.log("进入右滑")
this.openside();
this.swcount=0;
}
}else{
if(pageX>this.pageXI){
console.log("进入左滑");
this.openside();
this.swcount=0;
}
}*/

}
},
moveEnd(e) {
let pageX = e.changedTouches[0].pageX;
console.log(pageX + "===>" + this.pageXI)
/*左右滑*/
if(pageX + 55 < this.pageXI) {
if(this.infolist.length > 0) {
this.drawerVisibility = true;
}
this.swcount = 0;
} else if(pageX > this.pageXI + 55) {
this.drawerVisibility = false;
this.swcount = 0;
}
},
moveClick(e) {
this.swcount = 0;
console.log("moveClick==>" + e.changedTouches[0].pageX)
this.pageXI = e.changedTouches[0].pageX;
},
showBottomToast(val) {

this.$showBottomToast(val, 'bottom')
},
}
};



